<template>
  <div class="product_list">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">home</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/product/list' }"
          >产品列表</el-breadcrumb-item
        >
      </el-breadcrumb>
    </div>
    <div class="product_main">
      <div class="product_info" v-for="(item, index) of 6" :key="index">
        <img class="info_img" src="../../assets/img/pp.png" />
        <div class="info_content">
          <div class="info_content_title">
            这里是标题信息这里是标题信息这里是标题信息
          </div>
          <div class="info_content_txt">2020/02/09</div>
          <div class="info_content_txt">
            这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息…
          </div>
          <div class="info_content_txt">分发类型</div>
        </div>
      </div>
      <div class="paginaton">
        <el-pagination
          :background="false"
          :pager-count="9"
          layout="prev, pager, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :total="80"
        >
        </el-pagination>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
  },
  created() {},
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f9f9f9;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
}
.product_info {
  width: 1200px;
  height: 230px;
  box-shadow: 0 1px 0 0 #ededed;
  display: flex;
  padding: 24px;
  .info_img {
    width: 220px;
    height: 182px;
  }
  .info_content {
    width: 912px;
    margin-left: 20px;
  }
  .info_content_title {
    font-size: 16px;
    color: #333333;
    font-weight: bold;
    height: 22px;
    line-height: 22px;
    margin-bottom: 8px;
  }
  .info_content_txt {
    font-size: 14px;
    color: #787878;
    line-height: 20px;
    margin-bottom: 8px;
  }
}
</style>



